@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Volkhov&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0px;
}

.App {
  text-align: center;
  background-color: #111111;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #111111;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.banner{
  width: 100%;

}

.nav{
  width: 100%;
  height: 100px;
  z-index: 50;
  position: fixed;
  top: 0px;
  background: rgba(0, 0, 0, 0.71);
  padding: 15px 55px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.info{
  width: 100%;
  height: 1000px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flechasTitle{
  margin: 20px 8px;
  box-sizing: border-box;
}

.button{
  background-color: #F4A500;
  padding: 18px 27px;
  font-family: Raleway;
  font-size: 16px;
  color: white;
  margin-top: 45px;
  border-radius: 10px;
  border-width: 0px;
}

.content{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  /*margin-top: 100px;*/

  
}
.Link{
    font-family: Raleway;
    color: white;
    box-sizing: border-box;
    text-decoration: none;
    font-size: 17px;
    margin: 0px 25px;
}

.Link:hover{
    font-weight:bold;
    color: white;
}
.character{
width: 30%;
height: 500px;
display: flex;
flex-direction: column;
background-color: blue;
}

.characterName{
    font-family: Volkhov;
    font-weight: 400;
    margin: 0px;
    margin-left: 60%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.characterForm{
    width: 100%;
    padding-top: 160px;
    padding-left: 40px;
    box-sizing: border-box;
}

.characterForm label{
    width: 35%;
    font-size: 18px;
    font-family: Raleway;
    display: block;
    margin-top: 25px;
}

.characterForm input{
    width: 100%;
    display: block;
    background: rgba(255, 255, 255, 0.164);
    padding: 10px 10px;
    border-radius: 5px;
    border-color: white;
    color: white;
    margin-top: 5px;
    font-size: 16px;
    font-family: Raleway;
}

.titleForm{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-family: Anton;
    font-weight: 400;
}

.subtitleForm{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-top: 35px;
}

.largeInput{
    width: 100%;
    display: block;
    background: rgba(255, 255, 255, 0.164);
    padding: 10px 10px;
    height: 70px;
    border-radius: 5px;
    border-color: white;
    color: white;
    font-size: 16px;
    font-family: Raleway;
}

.CharacterForm_error{
    color: rgb(238, 22, 22);
    font-size: 16px;
}
